/* Everything needed */
:root {
    --theme-color: #242c38;
    --theme-color-text: #dee9ef;
    --theme-color-link: #d7c5e2;
}

@font-face {
    font-family: 'Wellfleet';
    src: url('./font/Wellfleet/Wellfleet-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Wellfleet', sans-serif;
}

.no-interaction {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -webkit-user-drag: none;
}

#root {
    width: 100%;
    min-height: 100vh;

    display: grid;
    grid-template-rows: auto 1fr auto;
}

/* Header and footer */
#header {
    background-color: var(--theme-color);
    padding: 10px 11px;
    width: calc(100% - 22px);
}

#header-left {
    width: max-content;
    color: var(--theme-color-text);
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
}

.logo-styles-1 {
    aspect-ratio: 1;
    width: 100px;
    border: solid black 1px;
    border-radius: 7px;
    transition: box-shadow 0.2s;
}

#header-left:hover {
    cursor: pointer;
}

#header-left:hover #header-logo {
    box-shadow: 2px 2px 4px rgba(0,0,0,0.3);
}

#header-left:hover #header-left-text {
    text-shadow: rgba(0,0,0,0.5) 3px 3px 6px;
}

#header-left:active {
    opacity: 0.7;
}


#footer {
    background-color: var(--theme-color);
    color: var(--theme-color-text);

    width: calc(100% - 20px);
    padding: 10px;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

#footer a {
    color: var(--theme-color-link);
}

#credit-lists-container {
    position: relative;
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(26%, 1fr));
    gap: 25px;
}

.credit-list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media (max-width: 850px) {
    #credit-lists-container {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(auto-fit, auto);
    }
}

/*PAGES*/
/*home*/
#homepage-container {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#get-image-form {
    display: flex;
    gap: 7px;
    flex-direction: column;
    align-items: center;
    width: 280px;
}

#get-image-form p {
    font-size: 1.8rem;
}

#get-image-form select, #get-image-form button {
    width: 80%;
}

#get-image-form select {
    background-color: white;
    border-radius: 4px;
    border: 1px solid black;
    color: black;
    padding: 0;
}

#get-image-form button {
    color: black;
    border: 1px solid black;
    border-radius: 4px;
    background-color: #EFEFEF;
    text-align: center;
    padding: 0;
}

#display-image-container {
    margin-top: 15px;
    margin-bottom: 15px;
    width: 50%;
    min-height: 60px;
    background-color: lightgray;
    padding: 10px;
}

#display-image-container img {
    width: 100%;
}

@media (max-width: 767px) {
    #display-image-container {
        width: 80%;
    }
}